.formContainer{
    box-sizing: border-box;
    margin-top: 30px;
}

.formContainer form{
    width: 75%;
    margin: 0 auto;
}
.inputField{
    margin-top: 27px;
}
.inputField label{
    font-size: 16px;
    font-weight: bold;
}
.inputField input{
    width: 502px;
    height: 40px;
    font-size: 16px;
    outline: none;
    border: none;
    outline-color: #ffffff;
    color: #000; 
}
.submitField{
    margin-top: 45px;
    color: #ffffff;
}
.submitField button{
    cursor: pointer;
    width: 511px;
    height: 60px;
    color: #F24472;
    font-weight: bolder;
    border: none;
    outline: none;
    font-size: 19px;
    letter-spacing: 0.05em;
    line-height: 30px;
    font-family: 'Cormorant Garamond', serif;
    box-shadow: -12px 10px 10px 2px rgb(0, 0, 0, 0.4);

}